<template>
  <div id="app">
    <header>
      <span>PWA VueJs</span>
    </header>
    <main>
      <div class="wrapper">
        <div class="books">
          <book v-for="list in lists" :key="list.imageUrl" :list="list"></book>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
  import data from './db.json'
  import book from './components/Book'

  export default {
    name: 'app',
    components: {
      book
    },
    data() {
      return {
        lists: []
      }
    },
    created() {
      this.lists = data
    }
  }
</script>

<style>
  .books {
    flex-wrap: wrap;
    justify-content: space-around;
    display: flex;
  }


</style>



