<template>
  <div class="book">
    <div class="booksDetails">
      <img :src="list.imageUrl" :alt="list.title">
      <h4>{{ list.title }}</h4>
      <p>{{ list.description }}</p>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'book',
    props: ['list']
  }
</script>

<style>
  .book {
    width: 18%;
    background: #F5F5F5;
    cursor: pointer;
    transition: all 100ms ease-in-out;
  }

  .book:hover {
    transform: translateY(-0.5em);
    background: #EBEBEB;
  }

  img {
    display: block;
    width: 100%;
  }
</style>



